  font-style: normal;

}

:root {
  font-size: 22px;
  font-family: raleway, system-ui;
  --main-bg-color1: rgb(69, 80, 124);
  --shadow: 5px 5px 10px rgb(0, 0, 0, 0.8);
}
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

body {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: aliceblue;
}

i {
  font-size: 28px;
}

/* Main Sections */

header {
  text-align: center;
  padding-top: 60px;
  background: linear-gradient(155deg,var(--main-bg-color1) 35%, aliceblue 35%);
}

main {
  flex: 1;
  background-color: aliceblue;
  text-align: center;
  margin-top: 60px;
  margin-bottom: 60px;
  padding: 20px;
}

footer {
  text-align: center;
  background-color: var(--main-bg-color1);
  margin-top: 60px;
  color: aliceblue;
}

/* H Elements */

h1 {
  color: aliceblue;
  width: 4ch;
  text-align: left;
  font-size: 2.5rem;
  filter: drop-shadow(2px 2px 10px black);
  position: relative;
  top: -170px;
  left: -70px;
}

h2 {
  width: 100%;
  text-align: center;
  font-size: 1.5rem;
  margin: 1rem 0px 1rem 0px;
}

h3 {
  flex-grow: 1;
  text-align: left;
  font-size: 1.3rem;
}

/* Sub Sections */

.mainImage {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.about-me {
  padding: 2rem;
}

.icon-links-about-me {
  visibility: visible;
  margin-top: 2rem;
}

.mainImage img {
  width: 300px;
  box-shadow: var(--shadow);
}

footer img {
 width: 100vw;
}

.projects-grid {
  display: grid;
  gap: 45px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-auto-rows: 500px;
}

.projects-grid li {
  display: flex;
  flex-direction: column;
  box-shadow: var(--shadow);
  transition: transform 500ms ease;
}

.projects-grid li:hover {
  animation-name: wiggle;
  animation-duration: 0.5s;
  animation-iteration-count: 1;
  animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
}

@keyframes wiggle {
  from {
    transform: rotate(0deg);
  }

  25% {
    transform: rotate(-1deg);
  }

  75% {
    transform: rotate(1deg);
  }

  to {
    transform: rotate(0deg);
  }
}

.projects-grid div {
  display: flex;
  justify-content: flex-start;
  gap: 16px;
  padding: 16px;
  align-items: center;
}

.projects-grid img {
  object-fit: cover;
  height: 280px;
}


svg, i {
  width: 28px;
  font-size: 28px;
}

.projects-grid p {
  padding: 16px;
  text-align: left;
}

.contact-details {
  display: flex;
  gap: 1rem;
  flex-direction: column;
  text-align: left;
  align-items: flex-start;
  padding: 16px;
}

.contact-details a {
  color: aliceblue;
  letter-spacing: 1px;
}

.icon-links {
  display: flex;
  width: 100%;
  justify-content: center;
  gap: 1rem;
}

.contact-svgs {
  fill: aliceblue;
  position: relative;
  top: 8px;
  margin-right: 16px;
  display: none;
}

@media (min-width: 1000px) and (max-width: 1199px) {

  :root {
    font-size: 26px;
  }

  header {
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(170deg,var(--main-bg-color1) 70%, aliceblue 70%);
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 140px;
  }

  .about-me {
    background-color: aliceblue;
    box-shadow: var(--shadow);
    max-width: fit-content;
    text-align: left;
    margin-top: 60px;
    width: 880px;
    height: auto;
    padding: 30px;
    text-wrap: pretty;
    margin-bottom: 60px;
  }

  .icon-links-about-me {
    visibility: hidden;
  }

  h1 {
    position: relative;
    width: max-content;
    top: -585px;
    left: 330px;
  }

  h2 {
    text-align: left;
  }

  h3 {
    font-size: 1rem;
  }

  header img {
    max-width: 400px;
    transform-origin: bottom;
    transform: scale(140%, 140%);
  }

  .mainImage {
    width: 250px;
    float: left;
    margin-right: 120px;
    height: 420px;
  }

  main {
    padding: 120px;
    margin-top: 0px;
  }

  svg, i {
    width: 28px;
    font-size: 28px;
  }

  footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  footer img {
    max-width: 500px;
    padding: 30px;
  }
}

.contact-svgs {
  fill: aliceblue;
  position: relative;
  top: 8px;
  margin-right: 16px;
  display: inline-block;
}

.projects-grid p {
  font-size: 0.8rem;
}

@media (min-width: 1200px) {

  :root {
    font-size: 26px;
  }

  header {
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(175deg,var(--main-bg-color1) 70%, aliceblue 70%);
    padding-left: 0px;
    padding-right: 0px;
    padding-top: 140px;
  }

  .about-me {
    background-color: aliceblue;
    box-shadow: var(--shadow);
    max-width: fit-content;
    text-align: left;
    margin-top: 15px;
    width: 1100px;
    height: 550px;
    padding: 15px;
    text-wrap: pretty;
    margin-bottom: 15px;
    position: relative;
  }

  .icon-links-about-me {
    visibility: hidden;
  }

  h1 {
    position: relative;
    font-size: 1.5rem;
    top: -70px;
    left: 60px;
    width: 100%;
  }

  h2 {
    text-align: left;
    margin-top: 0px;
  }

  header img {
    max-width: 400px;
  }

  h3 {
    font-size: 1rem;
  }

  .projects-grid p {
    font-size: 0.8rem;
  }

  .mainImage {
    width: 400px;
    height: 100%;
    float: left;
    margin-right: 50px;
    position: relative;
    top: 70px;
    left: -30px;
    transform: scale(160%, 160%);
  }

  main {
    padding: 120px;
    margin-top: 0px;
  }

  svg, i {
    width: 28px;
    font-size: 28px;
  }

  footer {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  footer img {
    max-width: 700px;
    padding: 50px;
  }

  footer .icon-links {
    display: flex;
    justify-content: flex-start;
  }

  .contact-details {
    width: 500px;
  }

  .contact-svgs {
    fill: aliceblue;
    position: relative;
    top: 8px;
    margin-right: 16px;
    display: inline-block;
  }
}
